import Index from "./index-model.js";
const index = new Index();
export default {
	data() {
		return {
			options: {},
			searchKey: '',
			// imageUrl: '',
			authorizationButton: true,
			userInfo: {},
			userInfoAll: {},
			// 轮播图相关
			bannerData: [],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			// 客户经理数据对象
			managerNode: {
				data: []
			},
			// 最新资讯
			informationNode: {
				data: []
			}
		}
	},
	onLoad(options) {
		const that = this
		that.options = options
		// that._onLoad()
	},
	onShow() {
		const that = this
		that._onLoad()
		// const that = this
		
		// let token = index.get_storage('token_type', callBack);
		// if(token) {
		// 	that.getUserInfo(() => {
		// 		that.getRunData(() => {
		// 			callBack && callBack();
		// 		})
		// 	})
		// }
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			// that.imageUrl = index.base_image_url
			that.userInfo = that.$store.state.userInfo;
			// 轮播图加载
			this.getBanner(() => {
				callBack && callBack();
			})
			
			// 客户经理列表加载
			this.getInformationList(() => {
				callBack && callBack();
			})
			// 获取已授权类别
			uni.getSetting({
				success(res) {
					if (res.authSetting['scope.userInfo']) {
						// 隐藏授权按钮
						that.authorizationButton = false;
						that.$store.commit('updateAuthorizationButtonData', false);
						that.wx_login(() => {
							// 客户经理列表加载
							that.getManagerList(() => {
								callBack && callBack();
							})
						})
					} else {
						// 隐藏底部导航
						uni.hideTabBar({
							boolean: true
						})
					}
				},
				fail() {
					console.log("获取授权信息授权失败")
				}
			})
		},
		// 进入客户经理列表
		goManagerList() {
			index.navigate_to(`/pages/managerList/managerList`);
		},
		// 进入资讯列表
		goArticleList() {
			index.navigate_to(`/pages/articleList/articleList`);
		},
		// 进入--客户经理--详情页
		goManagerDetails(e) {
			const that = this;
			// const id = index.get_data_set(e, "id");
			const managerindex = index.get_data_set(e, "managerindex");
			let data = JSON.stringify(that.managerNode.data[managerindex]);
			index.navigate_to(`/pages/managerDetails/managerDetails?data=${data}`);
		},
		// 进入--资讯--详情页
		goInformationDetails(e) {
			const that = this;
			const id = index.get_data_set(e, "id");
			index.navigate_to(`/pages/informationDetails/informationDetails?id=${id}`);
		},
		wx_login(callBack) {
			const that = this;
			// 显示底部导航
			
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					var code = loginRes.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							// 底部导航显示
							uni.showTabBar({
								boolean: true
							})
							that.userInfoAll = infoRes
							// 隐藏授权按钮
							that.authorizationButton = false;
							that.$store.commit('updateUserInfo', that.userInfo);
							that.$store.commit('updateAuthorizationButtonData', false);
							index.login({
								code: code,
								role: '', // 角色
								portrait: infoRes.userInfo.avatarUrl,
								nickname: infoRes.userInfo.nickName
							}, (res) => {
								// console.log(res)
								if (res.code == 4000) {
									that.userInfo = res.data
									that.$store.commit('updateUserInfo', that.userInfo);
									if (that.userInfo.role == "") {
										index.show_tips('检测到用户未选择权限类型，即将进入选择页')
										setTimeout(() => {
											index.navigate_to(`/pages/identitySel/identitySel`);
										}, 2000)
									}
								}
								callBack && callBack();
							})
						}
					})
				}
			});
		},
		getSearchCon(e) {
			const that = this
			that.searchKey = index.get_input_val(e)
			// searchKey不为空的时候执行搜索,为空的时候加载原有列表
			if (that.searchKey != '') {
				// console.log('======')
				// 执行搜索
				this.getSearchList()
			} else {
				// console.log('|||||||')
				// 客户经理列表加载
				this.getManagerList()
				// 客户经理列表加载
				this.getInformationList()
			}
		},
		// 执行搜索
		getSearchList(callBack) {
			const that = this
			index.getSearchList({
				openid: that.userInfo.openid,
				content: that.searchKey
			}, (res) => {
				if (res.code == '4000') {
					that.managerNode.data = res.ManagerList
					that.informationNode.data = res.NewsList
				}
				callBack && callBack();
			})
		},
		// 加载轮播图
		getBanner(callBack) {
			const that = this
			index.getBanner({}, (res) => {
				if (res.code == '4000') {
					that.bannerData = res.data
				}
				callBack && callBack();
			})
		},
		// 客户经理列表
		getManagerList(callBack) {
			const that = this
			index.getManagerList({
				openid: that.userInfo.openid
			}, (res) => {
				if (res.code == '4000') {
					that.managerNode = res
				}
				callBack && callBack();
			})
		},
		// 最新资讯
		getInformationList(callBack) {
			const that = this
			index.getInformationList({}, (res) => {
				if (res.code == '4000') {
					let list = res.data
					for (let i = 0; i < list.length; i++) {
						list[i].createtime = index.transformTime(list[i].createtime * 1000)
					}
					res.data = list
					that.informationNode = res
				}
				callBack && callBack();
			})
		},
		// 提示开通会员
		// promptOpenVip(callBack) {
		// 	uni.showModal({
		// 		title: '系统提示',
		// 		content: '系统检测到用户未开通会员，是否开通会员',
		// 		success: res => {
		// 			if (res.confirm) {
		// 				index.navigate_to(`/pages/vip/vip`);
		// 			} else if (res.cancel) {
		// 				console.log('用户点击取消');
		// 			}
		// 		}
		// 	});
		// }
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return index.onShareAppMessage({});
	}
}
